.button-fill-hover {
  letter-spacing: -.28px;
  border-radius: 12px;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  font-size: 14px;
  line-height: 14px;
  transition: background-color .6s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.button-fill-hover .text, .button-fill-hover .dot {
  z-index: 2;
  position: relative;
}

.button-fill-hover:after {
  content: "";
  z-index: 1;
  top: var(--mouse-y);
  left: var(--mouse-x);
  border-radius: 100%;
  width: 0;
  height: 0;
  transition: width .4s, height .4s;
  position: absolute;
  transform: translate(-50%, -50%);
}

.button-fill-hover:hover:after {
  width: 400px;
  height: 400px;
}

